.home {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100vh;
  gap: 100px;
  align-items: center;
}

@media (max-width: 992px) {
  .home {
    grid-template-columns: 1fr;
    height: 100%;
  }
}

@media (max-width: 480px) {
  .home {
    justify-content: center;
    gap: 50px;
  }
}
