* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-height: 100vh;
  margin: 0 auto;
  background-color: #454246;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 480px) {
  .container {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 930px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1070px;
  }
}

:root {
  --main-color: #a41177;
  --secondary-color: #423f3f;
  --thered-color: white;
}

svg {
  width: 100%;
  height: 100%;
}

