.formInputIneer {
  display: flex;
  align-items: start;
  flex-direction: column;
}

.label {
  font-family: sans-serif;
  color: #3d3944;
  font-size: 13px;
  margin-bottom: 3px;
}

.inp {
  border: none;
  border-radius: 4px;
  outline: none;
  margin-top: 2px;
  padding: 0 0 0 2px;
  width: 300px;
  height: 55px;
  padding-left: 10px;
  transition: 0.2s;
  background: #f0f0f0;
}

.inp:hover {
  box-shadow: 0px 0px 3px #ffffff;
}

@media (max-width: 480px) {
  .inp {
    width: 80%;
  }
}
