.ShowRes {
  display: flex;
  border: 1px solid wheat;
  padding: 31px 20px;
  background-color: #a41177;
  height: 212px;
  border-radius: 10px;
  box-shadow: 0 0 6px 1px #e6b99e;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

@media (max-width: 992px) {
  .ShowRes {
    margin-bottom: 100px;
  }
}

@media (max-width: 480px) {
  .ShowRes {
    width: 95%;
    margin: 30px auto;
    text-align: center;
  }
}
@media (max-width: 360px) {
  .ShowRes {
    width: 85%;
  }
}

.title {
  font-size: 26px;
  font-family: cursive;
  font-weight: bold;
  color: #f6dede;
}

@media (max-width: 360px) {
  .title {
    font-size: 24px;
  }
}

.title strong {
  color: #8bc34a;
  font-family: sans-serif;
}

.result {
  color: #f6dede;
  font-size: 33px;
  font-family: sans-serif;
}

@media (max-width: 360px) {
  .result {
    font-size: 30px;
  }
}

.initialRes {
  letter-spacing: 2px;
}
