.formInp {
  display: flex;
  border: 1px solid wheat;
  padding: 31px 20px;
  background-color: #a41177;
  height: 580px;
  border-radius: 10px;
  box-shadow: 0 0 6px 1px #e6b99e;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 480px) {
  .formInp {
    height: 615px;
    width: 95%;
    margin: 0 auto;
  }
}
@media (max-width: 320px) {
  .formInp {
    width: 90%;
  }
}

.formInpHeader {
  font-size: 28px;
  font-family: cursive;
  font-weight: bold;
  color: #f6dede;
  text-align: center;
}

@media (max-width: 480px) {
  .formInpHeader {
    font-size: 24px;
    padding: 0 20px;
    text-align: center;
    margin-bottom: 10px;
  }
}

.inputFormContainer {
  display: flex;
  justify-content: center;
  margin-top: 28px;
  height: 100%;
  width: 100%;
}

.inp {
  border: none;
  border-radius: 4px;
  outline: none;
  margin-top: 2px;
  padding: 0 0 0 2px;
  width: 300px;
  height: 55px;
  padding-left: 10px;
  transition: 0.2s;

  background: #f0f0f0;
}
@media (max-width: 480px) {
  .inp {
    width: 80%;
  }
}

.inp:hover {
  box-shadow: 0px 0px 3px #ffffff;
}

.label {
  font-family: sans-serif;
  color: #3d3944;
  font-size: 13px;
  margin-bottom: 3px;
}

.formInput {
  display: flex;
  flex-direction: column;
  gap: 47px;
}
.formInputIneer {
  display: flex;
  align-items: start;
  flex-direction: column;
}

.formInputInSub {
  display: flex;
  justify-content: center;
  align-items: center;
}

.submit {
  outline: none;
  border: none;
  border-radius: 8px;
  width: 265px;
  height: 53px;
  font-weight: bold;
  font-size: 18px;
  color: #454246;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}
@media (max-width: 480px) {
  .submit {
    margin-top: 40px;
  }
}

.submit:hover {
  color: #a41177;
  background-color: #454246;
  box-shadow: 0px 1px 2px 1px #6c6363;
}
