.proj_name {
  background-color: var(--main-color);
  text-align: center;
  padding: 25px;
  color: var(--main-color);
  font-size: 56px;
  letter-spacing: 31px;
  text-transform: uppercase;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header_name {
  background-image: linear-gradient(90deg, #ffffff, #090909);
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -o-background-clip: text;
  -ms-background-clip: text;
  color: transparent;
}

@media (max-width: 992px) {
  .proj_name {
    letter-spacing: 25px;
    font-size: 45px;
  }
}
@media (max-width: 769px) {
  .proj_name {
    letter-spacing: 10px;
    font-size: 45px;
  }
}

@media (max-width: 480px) {
  .proj_name {
    letter-spacing: 5px;
    font-size: 36px;
  }
}
@media (max-width: 320px) {
  .proj_name {
    letter-spacing: 5px;
    font-size: 33px;
  }
}

.custom_shape_divider_bottom_1695807464 {
  overflow: hidden;
  line-height: 0;
  transform: rotate(0deg);
}

.custom_shape_divider_bottom_1695807464 svg {
  position: relative;
  display: block;
  width: calc(108% + 1.3px);
  height: 119px;
  transform: rotateY(180deg);
}

.shape_fill {
  fill: var(--main-color);
}
